<template>
    <div class="uk-width-medium-3-4">
        <form v-on:submit.prevent="submit" class="uk-form uk-form-stacked">
            <div class="uk-alert uk-alert-danger uk-hidden"></div>
            <div class="uk-form-row">
                <label class="uk-form-label">标题:</label>
                <div class="uk-form-controls">
                    <input v-model="blog.title" name="title" type="text" placeholder="标题" class="uk-width-1-1">
                </div>
            </div>
            <div class="uk-form-row">
                <label class="uk-form-label">内容:</label>
                <div class="uk-form-controls">
                    <textarea id="div1" v-model="blog.content" name="content" placeholder="内容" class="uk-width-1-1" style="height:400px;max-height:500px;"></textarea>
                    
                </div>
            </div>
            <div class="uk-form-row">
                <button type="submit" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
                <a v-link="'/blog/'+blog.id" class="uk-button"><i class="uk-icon-times"></i> 取消</a>
            </div>
        </form>
    </div>
</template>

<script>
import { getUser } from '../vuex/getters'
export default {
  vuex: {
    getters: {
      user: getUser
    }
  },
  ready () {
    if (this.$route.params.id !== 'new') {
      this.$http.get('/api/blog', {
        params: {
          id: this.$route.params.id
        }
      }).then(function (res) {
        this.blog = res.data.blog
      }).then(function (err) {
        console.log(err)
      })
    }
  },
  methods: {
    submit: function () {
      this.$http.post('/api/blog_save', {
        id: this.$route.params.id,
        title: this.blog.title,
        content: this.blog.content,
        token: document.cookie.split('=')[1]
      }).then(function (res) {
        this.$route.router.go({name: 'blog', params: { id: res.data.blog_id }})
      }).then(function (err) {
        console.log(err)
      })
    }
  },
  data () {
    return {
      blog: ''
    }
  }
}
</script>



